/*HOME PAGE*/
@import url("https://fonts.googleapis.com/css?family=Playfair+Display|Source+Sans+Pro:200,400");

.TopNavBar {
  width: 100%;
}
.Loading_Img {
  width: 20px;
  border-radius: 50%;
}
.Upload {
  background-color: rgb(255, 136, 25);
  padding: 8px;
  border: none;
  color: #ffffff;
  text-decoration: none;
  border-radius: 10px;
  font-weight: bold;
  font-size: 13px;
  margin: 10px;
  cursor: pointer;
  width: fit-content;
  text-align: center;
}

.loading {
  width: 10%;
  margin-right: auto;
  margin-left: auto;
  margin-top: 15%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Playfair Display", serif;
}

p,
a {
  font-family: "Playfair Display", serif;
}

/* Generic styles */
html {
  scroll-behavior: smooth;
}

.wrapper a {
  background-color: rgb(255, 136, 25);
  text-decoration: none;
  color: white;
  border-radius: 0.25rem;
  text-align: center;
  display: inline-block;
  transition: all 0.3s;
  margin: 2px;
}

.wrapper a:hover {
  opacity: 0.6;
}

/* Styles for the hero image */
.hero {
  /* Photo by mnm.all on Unsplash */
  background-size: cover;
  padding: 4rem 2rem;
  /* grid styles */
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  align-items: center;
  width: 100%;
}

.hero > * {
  color: white;
}

.hero > h1 {
  font-size: 4rem;
  padding-bottom: 1rem;
}

.hero > article > p {
  font-size: 1.5rem;
  font-weight: 200;
}

.hero > article > a {
  padding: 1rem;
  margin-top: 0.75rem;
}
.webpage li {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  list-style-type: none;
  background-color: #ffffffc5;
  color: #333;
  font-size: 20px;
  font-weight: bolder;
}
Chart{
  color: #333;
  font-size: 20px;
  font-weight: bolder;
}
/* webpage styles */
.webpage {
  padding: 20px;
}

.webpage ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-gap: 1rem;
}

.webpage li {
  border: 1px solid #e2e2e2;
  border-radius: 0.5rem;
}

.webpage figure {
  max-height: 220px;
  overflow: hidden;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

.webpage img {
  width: 100%;
  padding: 10px;
  border-radius: 25px;
}

.webpage figcaption {
  position: absolute;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
}

.webpage p {
  font-size: 1rem;
  line-height: 1.5;
  padding: 1rem 0.75rem;
  color: #666666;
}

.webpage a {
  padding: 0.5rem 1rem;
  margin: 0.5rem;
}

/* footer */
footer {
  background-color: #333;
  padding: 0.75rem;
  color: white;
  text-align: center;
  font-size: 0.75rem;
}
