.modal {
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1000;
  height: 300vh;
}
.modal_content {
  top: 5%;
  bottom: 5%;
  left: 30%;
  width: 40%;
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  border-radius: 5px;
  z-index: 1001;
  position: relative;
  padding: 1rem;
  margin: 3rem auto 0 auto;
  top: 1rem;
  display: inline-block;
}

.close {
  color: rgb(255, 0, 0);
  float: right;
  cursor: pointer;
}

.modal01 {
