/*----------------------------------------------------------------------------------------------------------------------------------------*/

@media screen and (max-width: 1080px) {
  body{
    background-image: url(./Images/08.jpg);
    background-repeat: no-repeat;
    background-size: 150%;
  }
  .modal_content,
  .modal_content01 {
    width: 100%;
    top: 0;
    left: 0%;
    transform: scale(-20%, -30%);
  }

  .donthaveanaccount {
    text-align: center;
    width: 100%;
  }
  .forgotpasstest {
    text-align: center;
    width: 100%;
  }
  .logo {
    width: 50px;
  }
  .loading {
    margin-top: 50%;
    width: 45%;
  }
  .row {
    width: 100%;
  }

  .Submit {
    display: block;
    padding: 10px;
    text-decoration: none;
    cursor: pointer;
  }
  .Holder01,
  .Holder02 {
    display: block;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    float: left;
  }
  .Aboutstyle {
    width: 80%;
  }
  .container {
    display: block;
    width: 100%;
  }
  .imagetags {
    width: 90%;
  }
  .textcontents {
    text-align: justify;
    padding: 10px;
    font-weight: bold;
    width: 90%;
  }

  .heading02 {
    font-size: 20px;
  }

  .courses,
  .queries,
  .signin,
  .institutionalSignIn,
  .signup,
  .studentsignin,
  .facultysignin,
  .adminSignin,
  .forgotpassword,
  .addcourses,
  .updateuser,
  .createassets,
  .addjobs,
  .addExpenses,
  .uploadsingle,
  .uploadsingleoffice,
  .superaddcourses {
    width: 95%;
    margin-right: auto;
    margin-left: auto;
  }
  .Aboutstyle {
    width: 95%;
  }
  body {
    background-color: #e8e9eb;
  }
  .contact {
    width: 100%;
  }
  .Upload {
    padding: 2px;
    width: fit-content;
    cursor: pointer;
  }
}

/*
	Max width before this PARTICULAR table gets nasty. This query will take effect for any screen smaller than 760px and also iPads specifically.
	*/

.FileHolder {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.selectfile {
  border: none;
  width: 50px;
  padding: 20px;
}
input[type="file"] {
  border: none;
}
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(255, 136, 25);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(255, 136, 25);
}
